
*{
  padding: 0;
  margin: 0;
}
.load{
  background-image: url(/card/static/img/loading.a636b2c.jpg);
  background-size: cover;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999;
}
.icon{
  position: absolute;
  top: 1%;
  left: 89%;
  z-index: 99;
  transform: translateX(-50%);
  width: 60px;
}
.card{
  position: fixed;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}
html,body{
  height: 100%;
  overflow-y: hidden;
}
.bg{
  overflow-y: hidden;
  position: relative;
  height: 100vh;
  width: calc(100vh/362*2500);
  background-image: url(/card/static/img/bg.67437e7.svg);
  background-color: #3e0a9d;
  background-size: contain;
}
.person{
  width:280px;
  transition: .3s linear all;
  position:absolute;
}
.fade-enter-active{
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.down-enter-active{
  transition: transform .5s;
}
.down-enter, .down-leave-to /* .fade-leave-active below version 2.1.8 */ {
  transform: translateY(-100vh);
}
